"use client";
import React, { useEffect, useState } from "react";
import AuthGuard from "../../../components/AuthGuard";
import DashboardLayout from "../../../components/DashboardLayout";
import apiClient from "../../../lib/api";

function PastQuestionsPage() {
  const [showNotifications, setShowNotifications] = useState(false);
  const [notifications, setNotifications] = useState([]);
  const [unreadCount, setUnreadCount] = useState(0);

  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [pastQuestions, setPastQuestions] = useState([]);
  const [filteredQuestions, setFilteredQuestions] = useState([]);

  const [searchQuery, setSearchQuery] = useState("");
  const [selectedLevel, setSelectedLevel] = useState("all"); // 100,200,... or all
  const [selectedSemester, setSelectedSemester] = useState("all"); // 1,2 or all

  const levels = ["100", "200", "300", "400", "500"];
  const semesters = [
    { value: "1", label: "First Semester" },
    { value: "2", label: "Second Semester" },
  ];

  useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    try {
      setLoading(true);
      setError("");

      // Load questions (public endpoint)
      const pq = await apiClient.getPastQuestions();
      setPastQuestions(Array.isArray(pq.data) ? pq.data : []);

      // Load notifications (protected; ignore errors if unauthenticated)
      try {
        const notif = await apiClient.getNotifications();
        setNotifications(notif.data?.data || []);
        setUnreadCount(notif.data?.unread_count || 0);
      } catch (_) {
        // noop - user might be unauthenticated; layout still renders fine
      }
    } catch (e) {
      setError(e.message || "Failed to load past questions");
      setPastQuestions([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    let items = Array.isArray(pastQuestions) ? [...pastQuestions] : [];

    if (searchQuery) {
      const q = searchQuery.toLowerCase();
      items = items.filter(
        (it) =>
          (it.course_code || "").toLowerCase().includes(q) ||
          (it.course_title || "").toLowerCase().includes(q)
      );
    }

    if (selectedLevel !== "all") {
      items = items.filter((it) => String(it.level) === selectedLevel);
    }

    if (selectedSemester !== "all") {
      items = items.filter((it) => String(it.semester) === selectedSemester);
    }

    setFilteredQuestions(items);
  }, [searchQuery, selectedLevel, selectedSemester, pastQuestions]);

  const handleClearFilters = () => {
    setSearchQuery("");
    setSelectedLevel("all");
    setSelectedSemester("all");
  };

  const handleViewOrDownload = (question) => {
    if (!question?.id) return;
    const url = `${
      process.env.NEXT_PUBLIC_API_URL || "http://localhost:8000/api"
    }/past-questions/${question.id}/download`;
    window.open(url, "_blank");
  };

  return (
    <DashboardLayout
      showNotifications={true}
      notifications={notifications}
      unreadCount={unreadCount}
      onNotificationClick={() => setShowNotifications(!showNotifications)}
    >
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="bg-white rounded-2xl shadow-sm border border-gray-200 p-6 mb-6">
          <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
            <div>
              <h1 className="text-2xl md:text-3xl font-bold text-gray-900">
                Past Questions
              </h1>
              <p className="text-gray-600 mt-1">
                Browse verified past questions and download
              </p>
            </div>
            <button
              onClick={loadData}
              className="px-5 py-3 rounded-lg font-semibold text-white hover:opacity-90"
              style={{ backgroundColor: "#488bbf" }}
            >
              Refresh
            </button>
          </div>
        </div>

        {/* Error */}
        {error && (
          <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-6">
            {error}
          </div>
        )}

        {/* Search & Filters */}
        <div className="bg-white rounded-2xl shadow-sm border border-gray-200 p-6 mb-6">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            <div className="lg:col-span-2">
              <label className="block text-sm font-semibold text-gray-700 mb-2">
                Search
              </label>
              <input
                type="text"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                placeholder="Search by course code or title..."
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#488bbf] focus:border-transparent"
              />
            </div>

            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-2">
                Level
              </label>
              <select
                value={selectedLevel}
                onChange={(e) => setSelectedLevel(e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#488bbf] focus:border-transparent"
              >
                <option value="all">All Levels</option>
                {levels.map((lvl) => (
                  <option key={lvl} value={lvl}>
                    {lvl} Level
                  </option>
                ))}
              </select>
            </div>

            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-2">
                Semester
              </label>
              <select
                value={selectedSemester}
                onChange={(e) => setSelectedSemester(e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#488bbf] focus:border-transparent"
              >
                <option value="all">All Semesters</option>
                {semesters.map((s) => (
                  <option key={s.value} value={s.value}>
                    {s.label}
                  </option>
                ))}
              </select>
            </div>
          </div>
          <div className="mt-4">
            <button
              onClick={handleClearFilters}
              className="px-5 py-3 border border-gray-300 rounded-lg text-gray-700 font-medium hover:bg-gray-50"
            >
              Clear Filters
            </button>
          </div>
        </div>

        {/* Grid */}
        {loading ? (
          <div className="flex justify-center items-center h-64">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[#488bbf]"></div>
          </div>
        ) : Array.isArray(filteredQuestions) && filteredQuestions.length > 0 ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredQuestions.map((q) => (
              <div
                key={q.id || `${q.course_code}-${q.session}`}
                className="bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden hover:shadow-md transition"
              >
                <div className="p-6">
                  <div className="flex items-start justify-between mb-3">
                    <div className="flex-1">
                      <h3 className="text-lg font-bold text-gray-900">
                        {q.course_code || "N/A"}
                      </h3>
                      <p className="text-gray-600 text-sm mt-1 line-clamp-2">
                        {q.course_title || ""}
                      </p>
                    </div>
                    <span className="ml-4 inline-flex items-center px-3 py-1 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                      {q.level ? `${q.level} Level` : "Level"}
                    </span>
                  </div>

                  <div className="space-y-2 text-sm text-gray-600">
                    <div className="flex items-center">
                      <span className="mr-2">📅</span>
                      <span>{q.session || "Session"}</span>
                    </div>
                    <div className="flex items-center">
                      <span className="mr-2">🗓️</span>
                      <span>
                        {q.semester === 1 || q.semester === "1"
                          ? "First Semester"
                          : q.semester === 2 || q.semester === "2"
                          ? "Second Semester"
                          : "Semester"}
                      </span>
                    </div>
                  </div>

                  <div className="mt-4 flex justify-end">
                    <button
                      onClick={() => handleViewOrDownload(q)}
                      className="px-4 py-2 rounded-lg text-white font-medium hover:opacity-90"
                      style={{ backgroundColor: "#488bbf" }}
                      disabled={!q.id}
                      title={!q.id ? "Download unavailable" : "Download"}
                    >
                      {q.id ? "Download" : "View"}
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div className="bg-white rounded-2xl shadow-sm border border-gray-200 p-12 text-center">
            <div className="w-24 h-24 rounded-full bg-gray-100 mx-auto mb-4 flex items-center justify-center">
              <svg
                className="w-12 h-12 text-gray-400"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z"
                />
              </svg>
            </div>
            <h3 className="text-xl font-bold text-gray-900 mb-2">
              No past questions found
            </h3>
            <p className="text-gray-600 mb-6">
              {searchQuery
                ? `No results for "${searchQuery}". Try a different search term.`
                : "No past questions match your current filters. Try adjusting your filters."}
            </p>
            <button
              onClick={handleClearFilters}
              className="px-6 py-3 rounded-lg font-semibold text-white transition hover:opacity-90"
              style={{ backgroundColor: "#488bbf" }}
            >
              View All Past Questions
            </button>
          </div>
        )}

        {/* Info Box */}
        <div
          className="mt-6 rounded-2xl p-4 border"
          style={{ backgroundColor: "#e8f3f9", borderColor: "#488bbf" }}
        >
          <h4 className="font-semibold mb-2" style={{ color: "#488bbf" }}>
            How It Works
          </h4>
          <ul className="text-sm text-gray-700 space-y-1">
            <li>
              • Search by course code or title. Filter by level or semester.
            </li>
            <li>• Click Download to get the question document.</li>
            <li>• Upload your own past questions to help others.</li>
          </ul>
        </div>
      </div>
    </DashboardLayout>
  );
}

export default function ProtectedPastQuestionsPage() {
  return (
    <AuthGuard requireAuth={false}>
      <PastQuestionsPage />
    </AuthGuard>
  );
}
